:local {
  .base {
    width: 100%;
    height: 100%;
    padding-top: 30px;
    text-align: center;
  }


  .dropzoneContainer {
    width: 100%;
    max-width: 474px;
    margin: 20px auto 0;
  }

  .dropzone {
    width: 100%;
    height: 170px;
    border: 2px dashed rgb(153, 153, 153);
    border-radius: 5px;
    cursor: pointer;
    overflow: hidden;
  }

  .inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    transform: translateY(-3px);
  }

  .inner[disabled] {
    opacity: 0.5;
  }

  .progressDiv {
    width: 100%;
    margin: 20px 0;
    display: flex;
    align-items: center;
  }

  .filename {
    width: 75px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    padding-left: 10px;
  }

  .progressMeter {
    flex: 1;
    position: relative;
    margin: 0 3px 0 5px;
    transform: translateY(1px);
  }

  .progressBar {
    position: absolute;
    bottom: 0;
  }

  .progressPercent {
    position: absolute;
    right: 0;
    top: -20px;
    &::after {
      content: '%';
    }
  }

  .fileDelete {
    margin-left: 5px;
    cursor: pointer;
    padding-right: 10px;
  }

  input.mobileUploader[type=file] {
    display: none;
  }

  .message {
    font-weight: 500;
    padding: 0 20px;
  }

  .or {
    margin-top: 15px;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      width: 100px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, 50%);
    }
    &::after {
      content: 'or';
      background-color: inherit;
      padding: 0 4px;
    }
  }

  .buttonDiv {
    width: 100%;
  }
  .button {
    max-width: 300px;
    //font-size: $body1;
    margin-top: 15px;

    &:focus {
      outline: 0;
    }
  }

