@import '~src/css/fonts.scss';
@import '~src/css/colors.scss';

:local {
  .button {
    @include primary-font(bold);
    padding: 0;
    border: 0;
    border-radius: 4px;
    font-size: 18px;
    height: 47px;
    letter-spacing: .5px;
    line-height: 2;
    max-width: 321px;
    text-align: center;
    text-transform: uppercase;
    width: 100%;
  }

  .primary {
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  }

  .inverted {
    border: solid 2px;
  }
}

button:local(.disabled), button:local(.disabled)[disabled] {
  cursor: not-allowed;
  opacity: .4;
  box-shadow: none;
}
