@import '~src/css/fonts';
@import '~src/css/colors';
@import '~src/css/grid';
@import '~src/css/z-index';

:local {
  .closeButton {
    display: block;
    position: absolute;
    top: -12px;
    right: -12px;
    width: 24px;
    height: 24px;
    border-radius: 12px;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.5);
    text-align: center;
    line-height: 34px;
    color: $white;
    cursor: pointer;
    z-index: $CloseButton-zIndex;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
