$fallback-fonts: 'Helvetica Neue', Arial, Helvetica, sans-serif;
$lato: 'Lato', $fallback-fonts;
$base: 62.5%;
$primary-font: $lato;
$secondary-font: $lato;


// Add font weights here.
// Keep in mind that not all fonts will support all weights, and the browser
// will pick the closest available weight.
$font-weights: (
        light: 300,
        normal: 400,
        medium: 500,
        semibold: 600,
        bold: 700,
        black: 900,
        heavy: 900
);

@mixin primary-font($weight: normal) {
  font-family: $primary-font;
  font-weight: map-get($font-weights, $weight);
}

@mixin secondary-font($weight: normal) {
  font-family: $secondary-font;
  font-weight: map-get($font-weights, $weight);
}

@mixin lato($weight: normal) {
  font-family: $lato;
  font-weight: map-get($font-weights, $weight);
}

@mixin bold {
  font-weight: bold;
}

@mixin italic {
  font-style: italic;
}

@mixin bold-italic {
  @include bold;
  @include italic;
}

@function px-calc($size) {
  $pxSize: $size * 16 / 100%;
  @return $pxSize*1px;
}
$basePx: px-calc($base);

@function rem-calc($size) {
  $remSize: $size / $basePx;
  @return #{$remSize}rem;
}

$caption: rem-calc(11.8px);
$body2: rem-calc(13.8px);
$body1: rem-calc(15.8px);
$header6: rem-calc(19.7px);
$header5: rem-calc(23.7px);
$header4: rem-calc(33.6px);
$header3: rem-calc(47.4px);
$header2: rem-calc(59.7px);
$header1: rem-calc(95.56px);

:export { caption: $caption; }
:export { body2: $body2; }
:export { body1: $body1; }
:export { header6: $header6; }
:export { header5: $header5; }
:export { header4: $header4; }
:export { header3: $header3; }
:export { header2: $header2; }
:export { header1: $header1; }
