@import '~src/css/fonts.scss';
@import '~src/css/colors.scss';

:local(.header) {
  font-size: 14px;
  line-height: 40px;

  width: 100%;
  height: 40px;
  margin-bottom: 4px;

  text-align: center;
  text-transform: uppercase;

  color: $primary-color;
  fill: $primary-color;
  background-color: white;
  box-shadow: 0 1px 0 0 rgba(213, 213, 213, .5), 0 2px 4px 0 rgba(0, 0, 0, .1);

  @include secondary-font;
  &:hover {
    cursor: pointer;
  }
}

:local(.autoListingLink) {
  position: relative;
  bottom: 1px;

  display: inline-block;

  width: 10px;
  height: 7px;
  margin: 0 10px;

  vertical-align: middle;

  background: url('Arrow.png');
  background-size: 100%;
}
