@import '~src/css/colors';

$captureBtnSize: 66px;
$captureBtnSizeInside: $captureBtnSize - 14;
$captureBtnSizeInsideActive: $captureBtnSizeInside - 5;
$verticalPositionOfCaptureBtn: 80%;

:local {
  @keyframes shutter-animation {
    from { background-color: $white; }
    to { background-color: transparent; }
  }
  .shutterBlink {
    animation: shutter-animation;
    animation-duration: 700ms;
  }

  .backdrop{
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: $white;
    z-index: 9999;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .canvasContainer{
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    visibility: visible;
  }
  .cvCamera {
    width: 100%;
    height: 100%;
  }
  .hiddenCanvas{
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .riseZIndex{
    position: absolute;
    z-index: 2;
  }

  .cancelButton{
      font-size: 16px;
      color: white;
      opacity: 1;
    margin-left: 4%;
  }

  .headPart{
      position: fixed;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    display: flex;
    align-items: center;
    justify-content: space-between;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 50px; //12%;
  }

  .whiteBorder{
    width: $captureBtnSize;
    height: $captureBtnSize;
    position: fixed;
    left: 50%;
    margin-left: -$captureBtnSize/2;
    top: $verticalPositionOfCaptureBtn;
    border: 5px solid $white;
    border-radius: 50%;
  }

  .whiteCircle:active{
    width: $captureBtnSizeInsideActive;
    height: $captureBtnSizeInsideActive;
    position: absolute;
    left: 50%;
    margin-left: -$captureBtnSizeInsideActive/2;
    top: 50%;
    margin-top: -$captureBtnSizeInsideActive/2;
    background-color: $white;
    border-radius: 50%;
  }

  .whiteCircle{
    width: $captureBtnSizeInside;
    height: $captureBtnSizeInside;
    position: absolute;
    left: 50%;
    margin-left: -$captureBtnSizeInside/2;
    top: 50%;
    margin-top: - $captureBtnSizeInside/2;
    background-color: $white;
    border-radius: 50%;
  }

  .underLine{
    position: fixed;
    width: 36%;
    top: 98%;
    left: 50%;
    margin-left: -18%;
    height: 5px;
    border-radius: 5px;
    background-color: white;
  }

  .showTakenPhotoScreen, .takenPhoto{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  .video{
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: -1;
  }
  .hideCanvas{
    display: none !important;
  }

  .takenPhotoBtnsFrame{
    position: absolute;
    top: 75%;
    left: 0;
    width: 100%;
    height: 30%;
  }
  .container{
    position: fixed;
    top: 0%;
    left: 0;
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .hintContainer{
    //text-align: center;
    width: calc((100% - 59.5px));
    //border: 1px solid red;
    background: rgba(0,0,0, 0.5);
    color: #ffffff;
    padding: 10px 0px;
    border: none;
    z-index: 9999999999999999;

  }

  .hintText{
      color: #ffffff;
  }
}
